/* Modules */
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { SettingsComponent } from '../Components/settings/settings.component';
import { SettingPasswordComponent } from '../Components/settings/setting-password/setting-password.component';
import { SettingGeneralComponent } from '../Components/settings/setting-general/setting-general.component'

/* Services */
import { AuthenticationService } from '../Services/Authentication/authentication.service';

/* Librairies */

const routes: Routes = [
	{ path: '', canActivate: [AuthenticationService], component: SettingsComponent,
		children: [
			{ path: '', redirectTo: 'general', pathMatch: 'full' },
			{ path: 'general', component: SettingGeneralComponent },
			{ path: 'password', component: SettingPasswordComponent },
		]
	}
];

export const SETTINGS_ROUTES: ModuleWithProviders = RouterModule.forChild(routes);



