import { Component, OnInit } from '@angular/core';

/* Service */
import { AccountService } from '../../../Services/account.service';

@Component({
	selector: 'app-setting-password',
	templateUrl: './setting-password.component.html',
	styleUrls: [
		'./setting-password.component.scss',
		'../settings.component.scss'
	]
})
export class SettingPasswordComponent implements OnInit {

	form 		: any = {}
	pw_err 		: boolean
	new_pw_err 	: boolean
	confirm_err : boolean
	buttonText 	: string

	constructor(private AccountService: AccountService) { }

	ngOnInit() {
		this.form = {
			currentPassword : '',
			newPassword 	: '',
			confirmPassword : ''
		}
		this.buttonText = 'Save new password'
	}

	validatePassword(form) {
		this.new_pw_err = this.pw_err = this.confirm_err = true;

		if (form.newPassword) {
			this.new_pw_err = form.newPassword.length >= 8 ? false : true
			this.confirm_err = form.newPassword === form.confirmPassword ? false : true
		}
		return (this.pw_err && this.confirm_err)
	}

	updatePassword() {
		if (!this.validatePassword(this.form)) {
			this.AccountService.updatePassword({password: this.form.newPassword}).subscribe( res => {
				if (res.success) {
					this.buttonText = 'Saved'
					setTimeout( () => {
						this.buttonText = 'Save new password'
					}, 2000)
				}
			});
		}
	}
}



