import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Rx';

/* Services */
import { TokenService } from '../../../Services/Authentication/token.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { AccountService } from '../../../Services/account.service';
import { NetworksService } from '../../../Services/Networks/networks.service';

/* Interfaces */
import { networks } from '../../../Interfaces/Constants/networks-constant';

@Component({
	selector: 'app-setting-general',
	templateUrl: './setting-general.component.html',
	styleUrls: ['../settings.component.scss',
				'./setting-general.component.scss']
})
export class SettingGeneralComponent implements OnInit {

	networks 		: Object[]
	form 			: any = {}
	fn_err 			: boolean = false
	ln_err 			: boolean = false
	email_err 		: boolean = false
	verification1 	: boolean = false
	verification2 	: boolean = false
	email 			: string
	buttonText		: string
	searchNetwork 	: string
	emailNetwork	: string
	startupNetwork 	: Array<string> = networks
	user 			: any

	constructor(private TokenService: TokenService,
				private ProfilesService: ProfilesService,
				private AccountService: AccountService,
				private NetworksService: NetworksService) {
		this.user = this.TokenService.getToken().user
	}

	ngOnInit() {
		this.form = {
			first_name	: '',
			last_name 	: '',
			username 	: '',
			email 		: ''
		}
		this.buttonText = 'Save Changes'
		this.getProfile();
		this.getNetworks();
	}

	getProfile() {
		this.ProfilesService.getProfile(this.user.id).subscribe( res => {
			this.form = {
				first_name	: res.profile.first_name,
				last_name 	: res.profile.last_name,
				username 	: res.profile.username,
				email 		: this.user.email
			}

			this.searchNetwork = res.profile.name
		});
	}

	getNetworks() {
		this.NetworksService.getNetworks('university').subscribe( res => {
			this.networks = res.university
		});
	}

	selectNetwork(name, website) {
		this.searchNetwork = name
		this.networkVerification(name, website);
	}

	networkVerification(name, website) {
		this.verification1 = this.verification2 = false

		if (this.startupNetwork.indexOf(name) >= 0)
			this.verification2 	= true
		else {
			this.getEmailNetwork(website);
			this.verification1 	= true
		}
	}

	getEmailNetwork(website) {
		const position1 = website.indexOf('.') + 1
		const position2 = website.lastIndexOf('.');
		this.emailNetwork = 'email@' + website.slice(position1, position2) + '.edu'
	}

	onNetworkChange(event) {
		if (!event)
			this.verification1 = this.verification2 = false
	}

	validateForm(form) {
		this.fn_err = this.ln_err = this.email_err = true

		if (form.first_name)
			this.fn_err = false
		if (form.last_name)
			this.ln_err = false
		if (form.email) {
			const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
			this.email_err = !reg.test(form.email)
		}
		return (this.fn_err && this.ln_err && this.email_err)
	}

	updateAccount() {
		if (!this.validateForm(this.form)) {
			const body_profile = {
				first_name 	: this.form.first_name,
				last_name 	: this.form.last_name
			}
			const body_account = {
				username 	: this.form.username,
				email 		: this.form.email
			}

			Observable.forkJoin([
				this.AccountService.updateAccountInfo(body_account),
				this.ProfilesService.updateProfile(this.user.id, body_profile),
				this.NetworksService.joinNetwork({ email: this.email, network: this.searchNetwork || '' }),
			]).subscribe(res => {
				if (res[0].success && res[1].success) {
					this.buttonText = 'Saved'
					this.email = ''
					this.searchNetwork = ''
					setTimeout( () => {
						this.buttonText = 'Save Changes'
					}, 2000);
				} else 
					console.log(res);
			});
		}
	}

}



