import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';

/* Components */
import { SettingsComponent } from './settings.component';
import { SettingPasswordComponent } from './setting-password/setting-password.component';
import { SettingNotificationComponent } from './setting-notification/setting-notification.component';
import { SettingInvitationComponent } from './setting-invitation/setting-invitation.component';
import { SettingGeneralComponent } from './setting-general/setting-general.component'

/* Services */
import { AuthenticationService } from '../../Services/Authentication/authentication.service';
import { NetworksService } from '../../Services/Networks/networks.service';

/* Directives */
import { DirectivesModule } from '../../Directives/directives.module';

/* Library */

/* Route */
import { SETTINGS_ROUTES } from '../../Routes/settings.routes';

@NgModule({
	imports: [
		SETTINGS_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		PipeModule,
		FooterModule,
		DirectivesModule
	],
	exports: [
	],
	declarations: [
		SettingsComponent,
		SettingPasswordComponent,
		SettingNotificationComponent,
		SettingInvitationComponent,
		SettingGeneralComponent
	],
	providers: [
		AuthenticationService,
		NetworksService
	]
})
export class SettingsModule { }



